/* manrope-regular - cyrillic */
@font-face {
  font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
  font-family: 'Manrope';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/manrope-v13-cyrillic-regular.eot'); /* IE9 Compat Modes */
  src: url('../fonts/manrope-v13-cyrillic-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../fonts/manrope-v13-cyrillic-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/manrope-v13-cyrillic-regular.woff') format('woff'), /* Modern Browsers */
       url('../fonts/manrope-v13-cyrillic-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../fonts/manrope-v13-cyrillic-regular.svg#Manrope') format('svg'); /* Legacy iOS */
}
